
.cdz-container {
min-height: 100vh;
font-family: inherit;
background: linear-gradient(120deg, #c62828 60%, #2e7d32 100%);
color: #fff;
display: flex;
flex-direction: column;
justify-content: space-between;
}

.cdz-header {
padding: 32px 0 20px 0;
background: rgba(40,40,40,0.3);
text-align: center;
}

.cdz-header h1 {
font-size: 2.5rem;
letter-spacing: 2px;
}

.cdz-header h2 {
font-size: 1.3rem;
margin-top: 10px;
}

.cdz-form-section {
display: flex;
justify-content: center;
align-items: center;
}

.cdz-form {
background: rgba(183,28,28, 0.95);
padding: 32px 24px;
border-radius: 14px;
box-shadow: 0 4px 16px #2e7d32aa;
display: flex;
flex-direction: column;
min-width: 320px;
gap: 16px;
}

.cdz-form label {
display: flex;
flex-direction: column;
color: #fff;
margin-bottom: 8px;
}

.cdz-form input,
.cdz-form textarea {
margin-top: 5px;
border: none;
border-radius: 6px;
padding: 10px;
color: #2e7d32;
background: #fff;
font-weight: bold;
outline: none;
}

.cdz-form textarea {
min-height: 80px;
}

.cdz-form .cdz-submit {
background: #2e7d32;
color: #fff;
border: none;
font-size: 1rem;
font-weight: bold;
border-radius: 6px;
padding: 12px 0;
margin-top: 10px;
cursor: pointer;
transition: background 0.2s;
}

.cdz-form .cdz-submit:hover {
background: #388e3c;
}

.cdz-links {
display: flex;
justify-content: center;
margin: 30px 0 0 0;
gap: 18px;
}

.cdz-channel {
background: #fff;
color: #b71c1c;
font-weight: bold;
padding: 10px 24px;
border-radius: 8px;
text-decoration: none;
transition: background 0.2s, color 0.2s;
}

.cdz-channel:hover {
background: #b71c1c;
color: #fff;
}

.cdz-footer {
background: #2e7d32;
color: #fff;
text-align: center;
padding: 14px 0;
font-weight: 700;
letter-spacing: 1px;
}
